    height: 128px;
    padding: 40px 0;
  }
}

@media (width <= 1023.98px) {
  .header .menu .menu__body {
    background-color: #2c2c2c;
    width: 30%;
    padding: 86px 40px;
    transition: left .3s;
    position: fixed;
    top: 0;
    left: -100%;
  }
}

@media (width <= 767.98px) {
  .header .menu .menu__body {
    width: 100%;
    padding: 86px 20px;
  }
}

.menu-open .header .menu .menu__body {
  left: 0;
  overflow: auto;
}

.icon-menu {
  display: none;
}

@media (width <= 1023.98px) {
  .icon-menu {
    cursor: pointer;
    z-index: 5;
    flex: 0 0 24px;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    display: flex;
    position: relative;
  }

  .icon-menu:before, .icon-menu:after {
    content: "";
    background-color: #fff;
    width: 18px;
    height: 2px;
    transition: all .3s;
    position: absolute;
    left: 3px;
  }

  .icon-menu:before {
    top: 8px;
  }

  .icon-menu:after {
    bottom: 8px;
  }

  .menu-open .icon-menu:before {
    top: calc(50% - 1px);
    transform: rotate(-45deg);
  }

  .menu-open .icon-menu:after {
    bottom: calc(50% - 1px);
    transform: rotate(45deg);
  }
}

